body {
  margin: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-family: 'Montserrat', sans-serif;

    background: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  font-family: 'Montserrat';
}

input{box-sizing:border-box}

.form-container {
/*  border-color: white;
  border-style: solid;
  border-width: 1px;
  text-align: center; */
  width: 40%;
  margin: auto;
  margin-top: 6rem;
  height: 20%;
}

#headline {
  color: white;
  text-align: center;
  font-size: 2rem;
  font-family: 'Montserrat';
}

#task-input {
  font-family: 'Montserrat';
  position: relative;
  font-weight: 400;
  width: 100%;
  font-size: 1.5rem;
  color: white;
  background-color: #2c2e30;
  decoration: none;
  border-width: 1px;
  border-color: #2c2e30;
  border-radius: 6px;
  cursor: text;
}


::-webkit-input-placeholder {
  margin-left: 1rem;
  text-align: center;
  color: white;
  font-family: 'Montserrat';
  font-weight: 400;
  opacity: 0.5;
}

::-moz-placeholder {
  text-align: center;
  margin-left: 1rem;
  color: white;
  font-family: 'Montserrat';
  font-weight: 400;
  opacity: 0.5
}

#submit-button {
  position: relative;
  width: 100%;
  color: white;
  font-family: 'Montserrat';
  background-color: #007aff;
  border-color: #007aff;
  border-width: 1px;
  border-radius: 6px;
  font-size: 1.5rem;
  font-weight: 400;
  margin-top: 0.6rem;
  cursor: pointer;
}

button:focus {
  outline: 0;
}

input:focus {
  outline: 0;

}


.empty-error {
  color: white;
  width: 40%;
  margin: auto;
  text-align: center;
  margin-top: 3rem;
  font-size: 1.5rem;
  font-family: 'Montserrat';

}

.task-list {
  width: 40%;
  margin: auto;
/*  border-color: white;
  border-style: solid;
  border-width: 1px; */
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.task-item {
  font-family: 'Montserrat';
  color: white;
  background-color: #2c2e30;
  border-width: 1px;
  border-color: #2c2e30;
  border-radius: 6px;
  margin-bottom: 1rem;
  margin-top: 1rem;
  text-align: center;
  vertical-align: center;
  display: flex;
  flex-direction: row;
  align-items: baseline;
  height: 75px;
}

.delete-btn {

  font-family: 'Montserrat';
  width: 20%;
  background-color: #ff2d55;
  font-size: 1.5rem;
  border-radius: 6px;
  border-color: #ff2d55;
  color: white;
  margin-right: 0.8rem;
  cursor: pointer;
}




.white {
  color: white;
  position: relative;
  margin-left: 1rem;
  font-size: 1.5rem;
  font-weight: 400;
  width: 73%;
  text-align: left;
}
